<template>
  <div class="verification-page">
    <!-- Background Elements -->
    <div class="verification-background">
      <div class="bg-gradient"></div>
      <div class="bg-pattern"></div>
    </div>

    <div class="verification-container">
      <!-- Verification Card -->
      <div class="verification-card">
        <!-- Header -->
        <div class="verification-header">
          <div class="logo-container">
            <Icon icon="mdi:email-check" class="logo-icon" />
          </div>
          <h1 class="verification-title">Verifikasi Email</h1>
          <p class="verification-subtitle">
            Masukkan kode verifikasi yang telah dikirim ke<br>
            <strong>{{ userEmail }}</strong>
          </p>
        </div>

        <!-- Verification Form -->
        <el-form 
          ref="verificationForm" 
          :model="verificationForm" 
          :rules="verificationRules" 
          class="verification-form"
          @submit.native.prevent="handleVerification"
        >
          <el-form-item prop="code" class="form-item">
            <div class="code-input-container">
              <el-input
                v-model="verificationForm.code"
                placeholder="Masukkan 6 karakter kode verifikasi (huruf & angka)"
                prefix-icon="el-icon-key"
                size="large"
                class="form-input code-input"
                maxlength="6"
                @input="formatCode"
              />
            </div>
          </el-form-item>

          <el-form-item class="submit-item">
            <el-button 
              type="primary" 
              size="large" 
              :loading="loading"
              native-type="submit"
              class="verify-button"
            >
              <div class="button-content">
                <Icon icon="mdi:shield-check" class="button-icon" v-if="!loading" />
                <span>{{ loading ? 'Memverifikasi...' : 'Verifikasi Kode' }}</span>
              </div>
            </el-button>
          </el-form-item>

          <!-- Test Verification Button -->
          <!-- <el-form-item class="test-item">
            <el-button 
              type="success" 
              size="medium" 
              :loading="loading"
              @click="testVerification"
              class="test-verify-button"
            >
              <div class="button-content">
                <Icon icon="mdi:test-tube" class="button-icon" v-if="!loading" />
                <span>{{ loading ? 'Testing...' : 'Test Verification (ABC123)' }}</span>
              </div>
            </el-button>
          </el-form-item> -->
        </el-form>

        <!-- Resend Section -->
        <div class="resend-section">
          <p class="resend-text">
            Tidak menerima kode? 
            <el-link 
              type="primary" 
              @click="resendCode" 
              :disabled="resendDisabled"
              class="resend-link"
            >
              {{ resendDisabled ? `Kirim ulang dalam ${countdown}s` : 'Kirim ulang' }}
            </el-link>
          </p>
        </div>

        <!-- Footer -->
        <div class="verification-footer">
          <p class="back-text">
            <el-link type="info" @click="goBack" class="back-link">
              <Icon icon="mdi:arrow-left" class="back-icon" />
