            <p>Jika tidak ada di inbox, cek folder spam email Anda</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Icon } from "@iconify/vue2";
import { mapActions, mapState } from 'vuex';

export default {
  name: 'VerificationView',
  components: {
    Icon
  },
  computed: {
    ...mapState(['loading', 'message', 'status'])
  },
  watch: {
    // Watch for store message changes and display them
    message(newMessage) {
      if (newMessage) {
        this.$message.error(newMessage);
        // Clear message after showing it
        this.$store.commit('set_message', null);
      }
    }
  },
  data() {
    return {
      resendDisabled: false,
      countdown: 60,
      countdownTimer: null,
      userEmail: '',
      pendingData: null,
      verificationForm: {
        code: ''
      },
      verificationRules: {
        code: [
          { required: true, message: 'Kode verifikasi wajib diisi', trigger: 'blur' },
          { len: 6, message: 'Kode verifikasi harus 6 karakter', trigger: 'blur' },
          { pattern: /^[A-Za-z0-9]{6}$/, message: 'Kode harus berupa 6 karakter huruf dan angka', trigger: 'blur' }
        ]
      }
    }
  },
  mounted() {
    this.initializeVerification();
  },
  beforeDestroy() {
    if (this.countdownTimer) {
      clearInterval(this.countdownTimer);
    }
  },
  methods: {
    ...mapActions(['verify_email']),
    
    initializeVerification() {
      // Get pending verification data from localStorage
      const pendingVerification = localStorage.getItem('pending_verification');
      
      if (pendingVerification) {
        this.pendingData = JSON.parse(pendingVerification);
        this.userEmail = this.pendingData.email;
      } else {
        // If no pending data, redirect to register
        this.$message.warning('Tidak ada data verifikasi. Silakan daftar terlebih dahulu.');
        this.$router.push('/register');
      }
    },

    formatCode(value) {
      // Allow letters and numbers, limit to 6 characters, convert to uppercase
      const formattedValue = value.replace(/[^A-Za-z0-9]/g, '').slice(0, 6).toUpperCase();
      this.verificationForm.code = formattedValue;
    },

    async handleVerification() {
      this.$refs.verificationForm.validate(async (valid) => {
        if (valid) {
          try {
            // Prepare verification data for store action
            const verificationData = {
              email: this.userEmail,
              code: this.verificationForm.code
            };

            // Call store action for verification
            const response = await this.verify_email(verificationData);

            // Handle successful verification
            if (response.data) {
              // Store additional user data if provided
              if (response.data.user) {
                localStorage.setItem('user_data', JSON.stringify(response.data.user));
              }

              // Clear pending verification
              localStorage.removeItem('pending_verification');

              this.$message.success('Verifikasi berhasil! Akun Anda telah aktif.');
              
              // Redirect to login page
              setTimeout(() => {
                this.$router.push('/login');
              }, 2000);
            }
          } catch (error) {
            console.error('Verification error:', error);
            // Error handling is managed by store and watcher
          }
        } else {
          this.$message.error('Silakan periksa kode verifikasi Anda');
        }
      });
    },

    async resendCode() {
      if (this.resendDisabled) return;
      
      try {
        // Use the same request_verification action from store but call it as resend
        // For now, let's use direct HTTP call since resend might have different endpoint
        const response = await this.$http.post('http://localhost:8080/api/auth/resend-verification', {
          email: this.userEmail
        });

        if (response.data) {
          this.$message.success('Kode verifikasi baru telah dikirim ke email Anda');
          this.startCountdown();
        }
      } catch (error) {
        console.error('Resend code error:', error);
        this.$message.error('Gagal mengirim ulang kode. Silakan coba lagi nanti');
      }
    },

    startCountdown() {
      this.resendDisabled = true;
      this.countdown = 60;
      
      this.countdownTimer = setInterval(() => {
        this.countdown--;
        if (this.countdown <= 0) {
          this.resendDisabled = false;
          clearInterval(this.countdownTimer);
        }
      }, 1000);
    },

    goBack() {
      // Clear verification data and go back to register
      localStorage.removeItem('pending_verification');
      this.$router.push('/register');
    },

    // Test verification dengan kode contoh
    async testVerification() {
      const testCode = 'ABC123'; // Changed to alphanumeric test code
      this.verificationForm.code = testCode;
      
      try {
        // Use store action for test verification
        const verificationData = {
          email: this.userEmail,
          code: testCode
        };

        const response = await this.verify_email(verificationData);

        if (response.data) {
          this.$message.success('Test verifikasi berhasil!');
          console.log('Test verification successful:', response.data);
        }
      } catch (error) {
        console.error('Test verification error:', error);
        // Error will be handled by store watcher
      }
    }
  }
}
</script>

